<template>
<div id="app">
  <div class="container grid-960">
    <h1 class="title">Vue2Editor Demo</h1>

    <div class="flexWrapper">
      <div class="blogLabel">Summary</div>
      <vue-editor id="summary" v-model="content.summary"></vue-editor>
      <div id="summaryOutput" v-html="content.summary"></div>
    </div>
    <div class="flexWrapper">
      <div class="blogLabel">Content</div>
      <vue-editor id="body" v-model="content.body"></vue-editor>
      <div id="bodyOutput" v-html="content.body"></div>
    </div>
  </div>
</div>
</template>

<script>
import {
  VueEditor
} from '../src/index.js'

export default {
  components: {
    VueEditor,
  },
  data() {
    return {
      content: {
        summary: '<p>Summary Text</p>',
        body: '<h2>Body Text</h2>'
      }
    }
  },

  methods: {
  }
}
</script>

<style>

body {
  padding: 2em 1em;
}

.flexWrapper {
  display: flex;
  justify-content: space-between;
  margin: 2em 0;

  flex-flow: row wrap;
}

.flexWrapper .blogLabel {
  flex-basis: 100%;
  font-weight: 700;
  font-size: 1.75em;
  line-height: 1.75;
  margin-bottom: .25em;
  margin-top: 1em;
}
.flexWrapper > * {
  flex-basis: 48%;
  /*height: 400px;*/
}

#summary, #content {
  height: 350px;
}

.title {
  text-align: center;
  font-weight: 600;
  border-bottom: 2px solid #3F51B5;
}

</style>



